import React from "react";
import { Container } from "../Teoria/styles";

import { BiArrowBack } from "react-icons/bi";

import { Link } from "react-router-dom";

function Teoria() {
  return (
    <Container>
      <button id="voltar">
        <BiArrowBack size={24} color="#1cb0f6" />
        <Link to="/" style={{ textDecoration: "none", color: "#1cb0f6" }}>
          Voltar{" "}
        </Link>
      </button>
      <button id="pratica">Praticar</button>
      <h2>Numeral</h2>
      <p>
        Numeral  uma classe de palavras utilizada para determinar a quantidade
        ou a ordem de elementos numa sequncia. <br />
        Assim, quando dizemos que "Ele ganhou o prmio pela quinta vez" ou que
        "Esta turma  composta por 25 alunos", estamos usando numerais, seja
        para indicar uma ordem ("quinta") ou para falar de determinada
        quantidade ("25").
        <br />
        O numeral pode ser representado de duas maneiras:
        <br />
        <br />
        <b>Pelo smbolo numrico:</b> (1, 2, 3, 4, 1/2, 1/3, 3/4, 3., 5.,
        13....). Dentre os smbolos numricos, existem os algarismos arbicos
        (1, 2, 3, 4...) e os romanos (I, II, III, IV...).
        <br />
        <br />
        <b>Por extenso:</b> (um, dois, trs, cem, mil, tero, meio, dobro,
        dzia...).
        <br /> <br />
        H alguns tipos de numeral, que se diferenciam de acordo com a sua
        funo. Os numerais podem ser classificados em cardinais, ordinais,
        multiplicativos, fracionrios e coletivos. Vejamos cada um deles:
        <br />
        <br />
        <b>Numeral Cardinal</b>O numeral cardinal serve para,{" "}
        <b>nomear uma determinada quantidade</b>.<br /> "Um", "dois", "cinco",
        "dez", "cem", "mil" so numerais cardinais.
        <br />
        <b>Exemplo:</b>
        <br />
        Hoje cedo fui  padaria e comprei <b>oito</b> pes, <b>duas</b> baguetes
        e uma torta.Gastei <b>47</b> reais.
        <br />
        Os gastos da empresa no ano passado excederam os{" "}
        <b>dois milhes de reais.</b>
        <br />
        <br />
        <b>Numeral Ordinal </b>
        <br />O numeral ordinal tem a funo de dizer a <b>ordem</b> que
        determinado elemento (uma coisa, um ser, uma ideia...) ocupa numa
        sequncia. Se um atleta conquistou a medalha de bronze na Olimpada,
        usamos o numeral ordinal "terceiro" para nos referirmos  posio desse
        atleta na classificao final. "Segundo", "dcimo", " centsimo
        primeiro" e "septuagsimo quarto" so numerais ordinais.
        <br />
        <b>Exemplo</b>:<br />
        Meu apartamento fica no <b>dcimo primeiro</b> andar.<br/> 
        Fiquei em <b>sexagsimo terceiro</b> na lista final do concurso. <br />
        <br />
        <b>Numeral Multiplicativo</b> <br /> O numeral multiplicativo tem a
        funo de indicar aumento na quantidade, utilizando para isso numerais
        que representam multiplicaes. "Triplo", "dupla", "dobro" e "sxtuplo"
        so numerais multiplicativos. H sempre a opo de transmitirmos a ideia
        de multiplicao fazendo uso da palavra "vezes" aps um numeral
        cardinal. Assim, em vez de dizer "triplo", podemos dizer "trs vezes",
        ou em vez de dizer "sxtuplo", "seis vezes".<b>Exemplo</b>:<br /> Faturamos este ms o
        dobro do que faturamos no ms passado. Ex. 2: O nmero 11  undcuplo de
        um. (Undcuplo  onze vezes). <br />
        <br />
        <b>Numeral Fracionrio </b>
        <br />
        O numeral fracionrio serve para indicar diminuio da quantidade,
        fazendo uso de palavras que indicam fracionamento (diviso em partes).
        "Metade", "tero", "quinto" e "oitavo" so numerais fracionrios.
        Importante notar que,  exceo de "metade" ("meio") e "tero", todos os
        outros numerais fracionrios so representados com outros tipos de
        numerais. Por exemplo: "quinto" e "centsimo" so numerais ordinais que
        cumprem a funo de fracionrios. Outros numerais fracionrios fazem uso
        da palavra "avos" aps um numeral cardinal: "onze avos", "vinte e dois
        avos" etc. Ex. 1: Aquela TV custa um tero do meu salrio. Ex. 2: A
        vaquinha est sendo um sucesso! Faltam apenas doze avos do valor
        estipulado! <br />
        <br />
        <b>Numeral Coletivo</b>
        <br />O numeral coletivo faz referncia a um conjunto de elementos
        (meses, dias, seres, coisas...)."Dzia", "dezena", "semana" e "semestre"
        so numerais coletivos. Ex. 1: Pedro comprou uma dzia de ovos. Ex. 2:
        Por causa da epidemia, os tripulantes da embarcao ficaram de
        quarentena. Na designao de papas, reis, imperadores, sculos e parte
        de uma obra, utilizam-se os numerais ordinais at o dcimo. Depois,
        utilizam-se os cardinais. Por exemplo: Joo Paulo II (segundo) e o Bento
        XVI (dezesseis). Para leis, decretos e portarias, utiliza-se o numeral
        ordinal at o nono e o cardinal a partir do dez. J para os dias do ms,
        utilizam-se os cardinais, com exceo da indicao do primeiro dia do
        ms, feito pelo ordinal "primeiro".
      </p>
    </Container>
  );
}

export default Teoria;
