import React from "react";
import { Container } from "../Teoria/styles";

import { BiArrowBack } from "react-icons/bi";

import { Link } from "react-router-dom";

function Teoria() {
  return (
    <Container>
      <button id="voltar">
        <BiArrowBack size={24} color="#1cb0f6" />
        <Link to="/" style={{ textDecoration: "none", color: "#1cb0f6" }}>
          Voltar{" "}
        </Link>
      </button>
      <Link to="/Pronome1" style={{ textDecoration: "none", color: "#1cb0f6" }}>
        <button id="pratica">Praticar</button>{" "}
      </Link>
      <h2>Pronome</h2>
      <p>
        Pronome  a classe de palavras que substitui o substantivo (nome). Tem a
        finalidade de indicar a pessoa do discurso ou situar no tempo e espao,
        sem utilizar o seu nome.
        <br />
        <b>Pronome substantivo</b>  aquele que desempenha a funo de
        substantivo.
        <br />
        <b>Exemplo:</b>
        <br />
        <i>
          <b>Ela</b>
        </i>{" "}
         minha convidada.
        <br />
        <b>Pronome adjetivo</b>  aquele que acompanha ou modifica um
        substantivo.
        <br />
        <b>Exemplo:</b>
        <br />
        <i>
          <b>Minha</b>
        </i>{" "}
        caneta  azul,{" "}
        <i>
          <b>aquelas</b>
        </i>{" "}
        canetas so azuis.
      </p>
      <h3>Tipos de pronome:</h3>
      <p>
      <br />
        <b>Pronomes de tratamento</b> <br />
        Os pronomes de tratamento indicam o tratamento formal ou informal: Voc,
        Vossa Excelncia, Vossa Majestade... Saiba mais sobre os Pronomes de
        Tratamento. <br />

        <b>Pronomes relativos</b> <br />
        So aqueles que se referem a um termo mencionado anteriormente: cujo, o
        qual, as quais, quem... Estabelecem uma relao entre aquilo a que se
        referem e a afirmao que ser feita a seu respeito. <br />
        <b>Pronomes pessoais</b>
        <br />
        Os pronomes pessoais representam as pessoas do discurso (primeira,
        segunda ou terceira): eu, tu, ele/ela, ns, vs, eles/elas. Tambm so
        pronomes pessoais as formas que servem de complementos (me, mim, comigo,
        nos, conosco; te, ti, contigo, vos, convosco; o, a, lhe, se, si,
        consigo, os, as, lhes). Os pronomes pessoais ainda so divididos em:
        pronomes pessoais retos (quando exercem a funo de sujeito); e pronomes
        pessoais oblquos (quando substituem o substantivo e complementam os
        verbos). <br />
        <b> Pronomes demonstrativos</b> 
        <br />
        Pronomes demonstrativos indicam a posio de algo, situando no espao e tempo:
        este, isso, aquilo... Estes pronomes estabelecem uma relao entre o que
        representa ou determina e os trs mbitos do discurso: este, essa,
        aquele, etc. <br />
        <b>Pronomes possessivos</b>
        <br />
        Pronomes possessivos exprimem a noo de posse de algo: meu, teu, sua,
        vosso... Indica aquele a quem pertence aquilo que  referido no
        discurso. <br />
        <b>Pronomes indefinidos</b> 
        <br />
        Pronomes indefinidos indicam a quantidade do que representa de modo vago ou
        impreciso: ningum, algum, qualquer... <br />
        <b>Pronomes interrogativos</b>
        <br />
        Pronomes interrogativos servem para formular uma interrogao.
        Normalmente se trata de um pronome relativo usado para interrogar: quem,
        que, qual...
        <br />
      </p>
    </Container>
  );
}

export default Teoria;
